import { create } from "zustand";
import toast from "react-hot-toast";
import { api } from "@/lib/api";

export interface Camper {
  id: string;
  name: string;
  price: number;
  rating: number;
  location: string;
  description: string;
  form: string;
  length: string;
  width: string;
  height: string;
  tank: string;
  consumption: string;
  transmission: string;
  engine: string;
  AC: boolean;
  bathroom: boolean;
  kitchen: boolean;
  TV: boolean;
  radio: boolean;
  refrigerator: boolean;
  microwave: boolean;
  gas: boolean;
  water: boolean;
  gallery: { thumb: string; original: string }[];
  reviews: {
    reviewer_name: string;
    reviewer_rating: number;
    comment: string;
  }[];
}

interface TrucksStore {
  items: Camper[];
  loading: boolean;
  page: number;
  fetchFirstPage: () => Promise<void>;
  fetchNextPage: () => Promise<void>;
}

const LIMIT = 4;

export const useTrucksStore = create<TrucksStore>((set, get) => ({
  items: [],
  loading: false,
  page: 1,

  fetchFirstPage: async () => {
    try {
      set({ loading: true, page: 1 });

      const res = await api.get<Camper[]>("/campers", {
        params: { page: 1, limit: LIMIT },
      });

      set({ items: res.data, loading: false });
    } catch (e) {
      toast.error("Failed to load campers");
      set({ items: [], loading: false });
    }
  },

  fetchNextPage: async () => {
    try {
      const nextPage = get().page + 1;
      set({ loading: true });

      const res = await api.get<Camper[]>("/campers", {
        params: { page: nextPage, limit: LIMIT },
      });

      set({
        items: [...get().items, ...res.data],
        page: nextPage,
        loading: false,
      });
    } catch {
      set({ loading: false });
    }
  },
}));
